import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { OlioComponent } from './olio/olio.component';
import { ListsComponent } from './lists/lists.component';
import { CalendarsComponent } from './calendars/calendars.component';
import { ListComponent } from './list/list.component';
import { ConstructionComponent } from './construction/construction.component';

const routes: Routes = [
  {path:"", pathMatch: "full", redirectTo:"/dojo/calendars/mean"},
  {path: "dojo", component: HomeComponent},
  {path: "olio", component: ConstructionComponent},
  {path: "dojo/resources", component: ListsComponent, children:[
    {path:"", pathMatch: "full", redirectTo: "/dojo/resources/mid/mean"},
    {path:":phase", children: [
      {path: ":list", component: ListComponent}
    ] }
  ]},
  // {path: "dojo/resources/:phase/:list", component: ConstructionComponent},
  {path: "dojo/calendars/:stack", component: CalendarsComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
