import { Item } from "./item";

export class List {
    public list: Item   [];
    constructor(public title: string) {
        this.list = [];
    }

    addListItem(item: any)
    {
        this.list.push(item);
    }

    addListItems(items: Item[])
    {
        // console.log("listing!")
        for(let i of items)
        {
            // console.log(i);
            this.list.push(i);
        }
        // console.log(this.list);
        return this;
    }
}
