import { Component, OnInit } from '@angular/core';
import { Blurbs } from './datas';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  info_box: String;
  helloText: String = Blurbs.helloBlurb;
  calendarText: String = Blurbs.calendarBlurb;
  resourcesText: String = Blurbs.resourcesBlurb;
  olioText: String = Blurbs.olioBlurb;
  constructor() { }

  ngOnInit() {
    this.info_box="";
  }

  setInfo(val)
  {
    this.info_box = val==this.info_box?"":val;
  }

  amIInfo(val)
  {
    return {
      selected: (val == this.info_box),
      tile: true
    }
  }

  idle = () => ({idle: (this.info_box === "")});

}
