import { Component, OnInit, Input } from '@angular/core';
import { Day } from '../day';

@Component({
  selector: 'app-day',
  templateUrl: './day.component.html',
  styleUrls: ['./day.component.css']
})
export class DayComponent implements OnInit {

  @Input() day_data: Day;
  public show_goals: boolean;
  public show_algos: boolean;
  public show_lecture: boolean;
  public show_collab: boolean;
  public show_lunch: boolean;
  public show_afternoon: boolean;
  public show_eod: boolean;
  public show_reading: boolean;
  constructor() { }

  ngOnInit() {
    this.show_goals=false;
    this.show_algos=false;
    this.show_lecture=false;
    this.show_collab=false;
    this.show_lunch=false;
    this.show_afternoon=false;
    this.show_eod=false;
    this.show_reading=false;
  }

  show(section)
  {
    console.log(`toggling ${section}`);
    switch(section){
      case "goals": {
        this.show_goals = !this.show_goals;
        break;
      }
      case "algorithms": {
        this.show_algos = !this.show_algos;
        break;
      }
      case "lecture": {
        this.show_lecture=!this.show_lecture;
        break;
      }
      case "collab": {
        this.show_collab = !this.show_collab;
        break;
      }
      case "lunch": {
        this.show_lunch = !this.show_lunch;
        break;        
      }
      case "afternoon": {
        this.show_afternoon = !this.show_afternoon;
        break;        
      }
      case "eod": {
        this.show_eod = !this.show_eod;
        break;        
      }
      case "reading": {
        this.show_reading = !this.show_reading;
        break;          
      }
      default: {
        break;
      }
    }
  }

}
